                                <p className="wow fadeInUp" data-wow-delay=".5s">Customer (including the user of the website www.doctoroncall.org) complaints are part of the business life of any corporate entity. As being in a service sector and a service provider, customer satisfaction is the paramount objective for any Healthcare business. Doctor On Call Private Limited (DOC) believes that providing prompt and efficient service is extremely important not only to build confidence among new customers, but also to triumph over the existing customers.</p>
